.social {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 25px);
  gap: 11px;
  align-items: center;
}

.social a {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: $color-default-white;
  display: flex;
  justify-content: center;
  align-items: center;

  @include hover-focus {
    opacity: 0.8;
  }
}




.seo__wrapper {
  display: flex;
  flex-direction: column;
  background-color: #0A223E;

  @include vp-767 {
    flex-direction: row;
    padding-left: 24px;
    padding-right: 0;
  }

}

.seo__image {
  position: relative;
  width: 100%;
  overflow: hidden;


}

.seo__image img {
  width: 100%;
  object-fit: cover;

  @include vp-767 {
    height: 100%;
    // object-position: bottom 0 right 7px;
    object-position: center;
    position: relative;
    z-index: 1;
  }


}

.seo__list {
  font-size: 13px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: 0.39px;
  text-transform: uppercase;
  color: $color-default-white;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0 20px;

  @include vp-767 {
    padding: 40px 0;
    display: grid;
    grid-template-columns: 1fr auto;
    row-gap: 32px;
    min-width: 262px;
    position: relative;
    z-index: 3;
  }
}

.seo__list li {
  position: relative;
  margin-right: 48px;
  margin-bottom: 20px;
  padding-left: 20px;

  &::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 1px;
    top: 0;
    left: 0;
    background-color: white;
  }

  @include vp-767 {

    margin: 0;
    padding-top: 36px; //место для иконок
  }
}
